* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.nav {
    width: 100%;
    top: 0;
    position: fixed;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.404);
    display: -ms-flexbox;
    display: flex;
    margin: auto;
    padding: 1rem 0;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}

.nav ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    list-style: none;
}

.nav li {
    color: white;
    text-transform: capitalize;
    font-size: 1rem;
    cursor: pointer;
    margin: 0 20px;
}

.logo-img {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.logo-1 {
    width: 9rem;
    cursor: pointer;
}

.logo-2 {
    display: none;
}

.nav .nav-item {
    font-size: 1rem;
}

.menu {
    display: none;
}

@media only screen and (max-width: 1000px) {
    .nav {
        height: 4rem;
        background-color: rgb(0, 0, 0);
        -ms-flex-pack: justify;
            justify-content: space-between;
    }
    .nav ul {
        width: 100%;
        height: 100vh;
        top: 0px;
        right: -100%;
        padding-top: 40px;
        position: absolute;
        -webkit-transition: all 250ms ease-in-out;
        -o-transition: all 250ms ease-in-out;
        transition: all 250ms ease-in-out;
        background-color: rgb(0, 0, 0);
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: stretch;
            justify-content: stretch;
    }
    .nav li {
        color: white;
        font-size: 2rem;
        list-style: none;
        text-transform: capitalize;
        margin-left: 40px;
        margin-bottom: 20px;
    }
    @-webkit-keyframes anim {
        0% {
            -webkit-transform: translateX(100px);
                    transform: translateX(100px);
        }
        100% {
            -webkit-transform: translateX(0);
                    transform: translateX(0);
        }
    }
    @keyframes anim {
        0% {
            -webkit-transform: translateX(100px);
                    transform: translateX(100px);
        }
        100% {
            -webkit-transform: translateX(0);
                    transform: translateX(0);
        }
    }
    @-webkit-keyframes animRev {
        0% {
            -webkit-transform: translateX(0px);
                    transform: translateX(0px);
        }
        100% {
            -webkit-transform: translateX(100px);
                    transform: translateX(100px);
        }
    }
    @keyframes animRev {
        0% {
            -webkit-transform: translateX(0px);
                    transform: translateX(0px);
        }
        100% {
            -webkit-transform: translateX(100px);
                    transform: translateX(100px);
        }
    }
    .nav .nav-item {
        font-size: 1.4rem;
    }
    .logo-1 {
        width: 6rem;
        margin-left: 15px;
    }
    .menu {
        display: block;
        width: 20px;
        z-index: 1;
        margin-right: 15px;
    }
}


