.Sign-in {
    text-align: center;
}

.Sign-in h2 {
    font-size: 1rem;
    margin-top: 30px;
    margin-bottom: 10px;
}

.Sign-in h3 {
    font-size: 0.95rem;
    margin-top: 10px;
}

.Sign-in h4 {
    font-size: 1rem;
    text-align: start;
}

.Sign-in h5 {
    font-size: 0.9rem;
    text-align: start;
    color: rgb(83, 83, 83);
    text-decoration: underline;
}

.Sign-in h6 {
    font-size: 0.9rem;
    text-align: start;
    color: rgb(83, 83, 83);
    font-weight: 400;
    margin-left: 10px;
}

.Sign-in h7 {
    margin-top: 20px;
    font-size: 0.7rem;
    color: rgb(83, 83, 83);
}

.container {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.logo-bar {
    width: 100%;
    height: 110px;
    background-color: rgb(30, 215, 96);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.logo-4 {
    width: 200px;
    margin: 0;
}

.btn {
    width: 100%;
    height: 50px;
    outline: none;
    margin-top: 10px;
    border: none;
    border-radius: 30px;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    text-transform: uppercase;
    font-family: 'Mukta', sans-serif;
    letter-spacing: 0.1rem;
}

.btn-1 {
    background-color: #3B5998;
}

.btn-2 {
    background-color: black;
}

.btn-3 {
    background-color: white;
    color: #616467;
    border: 2px solid #616467;
    margin-bottom: 30px;
}

.btn-4 {
    background-color: rgb(30, 215, 96);
    width: 200px;
    font-size: 0.9rem;
    font-weight: bold;
    margin: 0;
}

input {
    width: 450px;
    height: 37px;
    margin: 10px 0;
    border: 1px solid #7676769b;
    font-size: 0.9rem;
    padding-left: 10px;
    outline: none;
}

input::-webkit-input-placeholder {
    font-size: 0.9rem;
    color: #616467;
}

input::-moz-placeholder {
    font-size: 0.9rem;
    color: #616467;
}

input::-ms-input-placeholder {
    font-size: 0.9rem;
    color: #616467;
}

input::placeholder {
    font-size: 0.9rem;
    color: #616467;
}

.div {
    width: 450px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 20px;
}

.div i {
    margin-right: 15px;
}

.div-2 {
    width: 450px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.check-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.check {
    width: 20px;
    height: 20px;
    background-color: green;
    cursor: pointer;
}


