.footer {
    background: black;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}

h6 {
    color: white;
}

/*Container -1*/

.cont {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.f1 {
    -ms-flex: 1 1;
        flex: 1 1;
}

.f2 {
    -ms-flex: 1 1;
        flex: 1 1;
}

.f3 {
    -ms-flex: 1 1;
        flex: 1 1;
}

.logo-3 {
    width: 138px;
    margin-left: 20px;
    margin-top: 50px;
}

.f2 ul {
    display: -ms-flexbox;
    display: flex;
}

.f2 .head {
    color: rgb(160, 159, 159);
    font-size: 0.8rem;
    margin-top: 40px;
}

.f2 li {
    color: white;
    font-weight: bold;
    text-decoration: none;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    list-style: none;
    font-size: 0.85rem;
    padding: 14px 0px;
    margin: 0 50px;
    font-weight: normal;
}

.f3 ul {
    display: -ms-flexbox;
    display: flex;
}

.f3 li {
    background-color: #222326;
    width: 53px;
    height: 53px;
    border-radius: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin: 10px;
}

.f3 i {
    color: white;
    font-size: 1.3rem;
}

/*Container -2*/

.f4 {
    -ms-flex: 2 1;
        flex: 2 1;
}


.f4 li {
    color: grey;
    font-size: 0.55rem;
    text-decoration: none;
    font-family: sans-serif;
    font-weight: bold;
    display: inline-block;
    margin-top: 50px;
    margin-bottom: 50px;
    margin-left: 20px;
}

.f5 {
    -ms-flex: 1 1;
        flex: 1 1;
}

@media only screen and (max-width: 1000px) {
    .logo-3 {
        width: 90px;
        margin-left: 20px;
        margin-top: 50px;
    }
    .cont {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: start;
            justify-content: start;
        -ms-flex-align: start;
            align-items: flex-start
    }
    .f2 ul {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .f2 li {
        margin: 0 20px;
    }
    .f3 ul {
        display: -ms-flexbox;
        display: flex;
    }
    .f3 ul {
        margin-top: 50px;
    }
    .f3 li{
        width: 45px;
        height: 45px;
    }
    .cont-2 {
        -ms-flex-direction: row;
            flex-direction: row;
    }
}


