.Home h1 {
    font-size: 5rem;
    color: white;
    text-align: center;
}

.Home h2 {
    font-size: 4rem;
    color: white;
}

.Home h3 {
    font-size: 3rem;
    color: black;
    text-align: center;
}

.Home h4 {
    font-size: 1.5rem;
    color: black;
}

.Home h5 {
    font-size: 1rem;
    color: black;
}

.main-content {
    width: 100%;
    height: 600px;
    background-color: rgb(30, 215, 96);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: stretch;
        justify-content: stretch;
}

.phone {
    width: 550px;
    margin-top: 100px;
}

.row-1 {
    display: block;
}

.row-2 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.free-spotify-btn {
    background-color: white;
    color: black;
    font-size: 0.9rem;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: bold;
    border-radius: 100px;
    border: none;
    width: 250px;
    padding: 16px 0px;
    margin-top: 25px;
    margin-left: 0;
    letter-spacing: 0.1rem;
}

/*Second Content*/

.second-content {
    margin: 50px auto;
    background: white;
    width: 100%;
    text-align: center;
}

.cards {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}

.box {
    background-color: rgb(30, 215, 96);
    color: #ffffff;
    margin-top: 40px;
    margin-bottom: 40px;
    border-radius: 100%;
    font-size: 4rem;
    width: 170px;
    height: 170px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
    left: 50%;
    -webkit-transform: translate(-50%);
        -ms-transform: translate(-50%);
            transform: translate(-50%);
}

.black-color {
    color: black;
}

/*Third Content*/

.third-content {
    background-color: rgb(30, 215, 96);
    width: 100%;
    padding: 12rem;
}

.third-content h3 {
    color: white;
}

/*Fourth Content*/

.fourth-content {
    width: 100%;
}

.fourth-content h3 {
    margin: 50px 0;
}

.fourth-content table {
    margin: auto auto 4rem auto;
    width: 60%;
    background-color: rgb(243, 243, 243);
    border: 0.3px solid lightgrey;
    border-spacing: 0;
}

.fourth-content table td {
    text-decoration: none;
    color: black;
    padding: 7px 12px;
    font-size: 1.1rem;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border: 0.1px solid lightgrey;
    padding: 40px;
}

/*Fifth Content*/

.fifth-content {
    background-color: rgb(30, 215, 96);
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 150px 0;
}

.btn-align {
    -ms-flex-item-align: center;
        align-self: center;
}

@media only screen and (max-width: 1000px) {
    .main-content {
        height: 400px;
        margin-top: 4rem;
    }
    .row-1 {
        display: none;
    }
    .row-2 {
        margin: 20px;
    }
    /*Second Content*/
    .second-content {
        width: 100%;
    }
    .second-content h3 {
        margin: 40px 0;
    }
    .cards {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .box {
        font-size: 3rem;
        width: 150px;
        height: 150px;
    }
    .black-color {
        color: black;
    }
    /*Third Content*
    
    /

    /*Fourth Content*/
    .fourth-content table {
        margin-top: 40px;
        width: 100%;
    }
    /*Fifth Content*/
    .fifth-content {
        height: 350px;
    }
}

@media only screen and (max-width: 700px) {
    .row-2 h2 {
        font-size: 3rem;
    }
}

@media only screen and (max-width: 500px) {
    .row-2 h2 {
        font-size: 2rem;
    }
}


